import { TreeView, TreeItem } from "@material-ui/lab";
import { MdExpandLess, MdExpandMore } from "react-icons/md";
import { uuidv4 } from "uuid";

const toTreeItems = object => {
  const entries = Object.entries(object);
  return (
    <>
      {entries.map(([key, value]) => {
        switch (typeof value) {
          case "number":
            return (
              <TreeItem nodeId={uuidv4()} label={key}>
                <TreeItem nodeId={uuidv4()} label={value.toString()} />
              </TreeItem>
            );
          case "string":
            return (
              <TreeItem nodeId={uuidv4()} label={key}>
                <TreeItem nodeId={uuidv4()} label={value} />
              </TreeItem>
            );
          default:
            return (
              <TreeItem nodeId={uuidv4()} label={key}>
                <TreeItem nodeId={uuidv4()} label={typeof value} />
              </TreeItem>
            );
        }
      })}
    </>
  );
};

export const ObjectTree = ({ object }) => (
  <TreeView
    defaultCollapseIcon={<MdExpandMore />}
    defaultExpandIcon={<MdExpandLess />}
  >
    {toTreeItems(object)}
  </TreeView>
);
